@import url(https://fonts.googleapis.com/css2?family=Work+Sans:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
body {
  margin: 0;
  font-family: 'Work Sans', 'Arial', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html, body {
  height: 100%;
  width: 100%;
  margin: 0;
  padding: 0;
}

.App {
  font-family: 'Work Sans', sans-serif;
  /* background-image: linear-gradient(120deg, #a6c0fe 0%, #f68084 100%); */
}

.cover {
  height: 100vh;
  background-image: linear-gradient(120deg, #a6c0fe 0%, #f68084 100%);
  color: #FAFAFA;
}

.glass {
  background: rgba( 255, 255, 255, 0.2);
  box-shadow: 0 8px 32px 0 rgba( 31, 38, 135, 0.37);
  backdrop-filter: blur(4px);
  -webkit-backdrop-filter: blur(4px);
  border-radius: 20px;
  height: 50vh;
  width: 50vw;
  margin: auto;
}

.cover-container {
  text-align: center;
  padding-top: 10vh;
}

.intro-title {
  text-align: center;
  font-size: 64px;
  font-weight: 700;
  letter-spacing: 0.03em;
  animation-duration: 2000ms;
  animation-name: fadeIn;
}

.intro-subtitle {
  text-align: center;
  font-size: 26px;
  font-weight: 500;
  padding-top: 2rem;
  width: 35vw;
  margin: auto;
  animation-duration: 3000ms;
  animation-name: fadeIn;
}

@keyframes fadeIn {
  0% {opacity:0;}
  100% {opacity:1;}
  }
.navbar {
  height: 7vh;
  width: 40vw;
  background: rgba( 255, 255, 255, 0.2);
  box-shadow: 0 8px 32px 0 rgba( 31, 38, 135, 0.37);
  backdrop-filter: blur(4px);
  -webkit-backdrop-filter: blur(4px);
  border-radius: 0px 0px 50px 50px;
  margin: auto;
}

.navBarLink {
  margin: 2rem;
  font-size: 20px;
  font-weight: 600;
  letter-spacing: 0.05em;
  color: #FAFAFA !important;
}

.navBarLink:hover {
  background: rgba( 255, 255, 255, 0.05);
  box-shadow: 0 8px 32px 0 rgba( 31, 38, 135, 0.37);
  backdrop-filter: blur(4px);
  -webkit-backdrop-filter: blur(4px);
  border-radius: 10px;
  color: #FAFAFA;
}
#about {
  height: 100vh;
}

.about-row {
  align-items: center;
  padding-top: 15vh;
  padding-bottom: 15vh;
}

.pic-col {
  display: flex;
  align-items: center;
}

.pic {
  width: 40vh;
  display: block;
  margin-left: auto;
  margin-right: auto;
  animation:slideinleft 1s forwards;
}

.about-title {
  color: #4D4D4D;
  font-size: 36px;
  font-weight: 700;
  padding-bottom: 1rem;
  text-align: left;
  animation: slideinright 1s forwards;
}

.about-text {
  color: #4D4D4D;
  font-size: 20px;
  text-align: left;
  vertical-align: auto;
  animation: slideinright 1s forwards;
}

@keyframes slideinleft{
  0% {transform: translateX(-50%); opacity: 0;}
  100% {transform: translateX(0%); opacity: 1;}
}

@keyframes slideinright {
  0% {transform: translateX(50%); opacity: 0; }
  100% {transform: translateX(0%);  opacity: 1;}
}

@media (max-width: 991px) {
  .about-row {
    flex-direction: column;
  }
  .pic-col {
      width: 100vw;
  }
  .about-title {
      text-align: center;
  }
  .text-col {
    width: 100vw;
    text-align: center;
    padding-top: 3em;
}
}
#work {
  position: relative;
  height: 1080px;
  background-color: #F9D184;
}
#work h1 {
  text-align: center;
  font-size: 36px;
  font-weight: 700;
  color: #FAFAFA;
  padding-top: 2em;
}
#work h2 {
  font-weight: 600;
  font-size: 28px;
  color: #4D4D4D;
}
#work h3 {
  font-weight: 500;
  font-size: 20px;
  color: #AFAFAF;
}
#work .project {
  display: inline-block;
  width: 800px;
  height: 400px;
  border-radius: 20px;
  background: #FAFAFA;
}
#experience {
  position: relative;
  height: 1080px;
}
#experience .job{
  padding-bottom: 1.5em;
}
#experience h1 {
  text-align: center;
  font-size: 36px;
  font-weight: 700;
  color: #F27E59;
  padding: 2em;
}
#experience h2 {
  font-size: 28px;
  font-weight: 600;
  color: #4D4D4D;
}
#experience h3 {
  font-size: 20px;
  font-style: italic;
  color: #AFAFAF;
}
#experience p {
  font-size: 24px;
  color: #4D4D4D;
}
#experience .icon {
  height: 60px;
}
#experience .resume {
  text-align: center;
  padding-top: 3em;
}
#experience Button {
  font-size: 24px;
  color: #FAFAFA;
  background: #F27E59;
  border-color: #F27E59;
  border-radius: 20px;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  padding: 0.5em 1em;
}
#contact {
  position: relative;
  height: 300px;
  background-color: #69AC78;
  color: #FAFAFA;
  text-align: center;

}
#contact h1 {
  font-size: 36px;
  font-weight: 700;
  padding-top: 1em;
}
#contact h2 {
  font-size: 20px;
}
